}
body{
    height: calc(100vh-8em);
    padding:4em;
    color: rgba(255,255,255,0.75);
    font-family:'Anonymous Pro',monospace;
    background-color:rgb(25,25,25)
}

.Profile_line__2Ftft{
    position: relative;
    top: 50%;
    width: 16em;
    margin: 0 auto;
    border-right: 2px solid rgba(255,255,255,0.75);
    font-size: 150%;
    text-align:center;
    white-space: nowrap;
    overflow: hidden;
    transform:translateY(-50%)
}
/*Animation*/
.Profile_anim_typewriter__3e6NX{
    -webkit-animation: Profile_typewriter__YfdO3 4s steps(40) 1s 1 normal both;
            animation: Profile_typewriter__YfdO3 4s steps(40) 1s 1 normal both;
    blinkTextCursor: 500ms steps(40) infinite normal;
}

@-webkit-keyframes Profile_typewriter__YfdO3{
    from {
        width: 0;
    }
    to{
        width: 16em;
    }
}

@keyframes Profile_typewriter__YfdO3{
    from {
        width: 0;
    }
    to{
        width: 16em;
    }
}
@-webkit-keyframes Profile_blinkTextCursor__jRvoB{
    from {
        border-right-color: rgba(255,255,255,0.75);
    }
    to{
        border-right-color: transparent;
    }
}
@keyframes Profile_blinkTextCursor__jRvoB{
    from {
        border-right-color: rgba(255,255,255,0.75);
    }
    to{
        border-right-color: transparent;
    }
}
